import { _decorator, AudioSource, Color, Component, LabelComponent, Node, SpriteComponent, Vec3, tween, AudioClip, SpriteFrame, Animation } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Balloon')
export class Balloon extends Component {
    @property
    minScale: number = 1; // 

    @property
    maxScale: number = 10; // 

    @property
    minRotationSpeed: number = 10; //

    @property
    maxRotationSpeed: number = 1000;

    @property(Color)
    colors: Color[] = [];

    @property(LabelComponent)
    label: LabelComponent = null;

    @property(SpriteComponent)
    balloonSprite: SpriteComponent = null;

    @property([SpriteFrame])
    public spriteFrames: SpriteFrame[] = [];

    @property(AudioClip)
    public clip: AudioClip = null!;

    @property(AudioSource)
    public audioSource: AudioSource = null!;

    @property(Node)
    public balloonParticle: Node = null;
    @property
    public spawnTime: number = 0; // 
    // 
    private originalScale: Vec3 = new Vec3();
    _rotationDirection: number = 1; // 

    difficulty: number = 1; //  GameController 

    rotationSpeed: number = 1; //
    onLoad() {
        this.node.setScale(0, 0); //  scale  0
        //  _audioSource 
        //
        this.setRandomSpriteFrame();
        // 
        this.setRandomColor();
        // 
        this.setRandomRotationDirection();
        // 


    }

    start() {
        this.node.on(Node.EventType.TOUCH_START, this.onBalloonClicked, this);
        //  onLoad 
    }

    update(deltaTime: number) {
        // 
        this.rotateBalloon(deltaTime);
    }

    rotateBalloon(deltaTime: number) {
        // 
        const rotateAngle = this.rotationSpeed * deltaTime * this._rotationDirection;
        //  Z 
        this.node.angle += rotateAngle;
    }

    playOneShot() {
        this.audioSource.playOneShot(this.clip, 1);
    }

    onBalloonClicked(event) {
        this.playOneShot();
        this.applyElasticDeformation();
        // 
        const animation = this.getComponent(Animation);
        if (animation) {
            // 
            animation.once(Animation.EventType.FINISHED, this.onAnimationFinished, this); // 
            animation.play('BalloonExplode'); // 
        } else {
            // 
            this.node.destroy();
        }
    }
    onAnimationFinished() {
        // 
        this.node.destroy();
    }
    // 
    setNumber(number: number, spawnTime) {
        this.spawnTime = spawnTime; // 
        if (this.label) {
            this.label.string = number.toString();
        }
    }

    setUniqueScale(scale: number) { // 
        // Vec3
        const targetScale = new Vec3(scale, scale, scale);
        // Tween
        tween(this.node)
            .to(1, { scale: targetScale }, { easing: 'quartOut' }) // 
            .start(); // Tween
    }
    setRandomShowEffect(scale: number) { // 
        this.balloonParticle.active = false;
    }
    setRandomSpriteFrame() {
        if (this.spriteFrames.length > 0) {
            // 
            const randomIndex = Math.floor(Math.random() * this.spriteFrames.length);
            const randomSpriteFrame = this.spriteFrames[randomIndex];
            //  SpriteComponent 
            const spriteComponent = this.node.getComponent(SpriteComponent);
            if (spriteComponent) {
                spriteComponent.spriteFrame = randomSpriteFrame;
            }
        } else {
            console.warn("No sprite frames defined in the spriteFrames array.");
        }
    }
    getScaleSize(): number {
        // xy

        return this.node.scale.x;
    }
    setRandomColor() {
        if (this.colors.length > 0) {
            // 
            const randomIndex = Math.floor(Math.random() * this.colors.length);
            const randomColor = this.colors[randomIndex];

            // 
            if (this.balloonSprite) {
                // console.log("", randomColor);
                this.balloonSprite.color = randomColor;
            }
        } else {
            console.warn("No colors defined in the colors array.");
        }
    }

    applyElasticDeformation() {
        //  
        const targetScale = new Vec3(this.node.scale.x * 1.5, this.node.scale.y * 1.5, 1); // 10%
        tween(this.node)
            .to(0.1, { scale: targetScale }, { easing: 'quartOut' }) // 
            .call(() => {
                // 
                this.originalScale.set(this.node.scale);
            })
            .to(0.1, { scale: this.originalScale }, { easing: 'quartOut' }) // 
            .start();
    }


    getRandomScale() {
        //  Vec3 
        const randomScaleValue = Math.random() * (this.maxScale - this.minScale) + this.minScale;
        // console.log("", randomScaleValue);
        return new Vec3(randomScaleValue, randomScaleValue, 1); // 2DZscale1
    }

    setRandomRotationDirection() {
        // 1 -1 
        this._rotationDirection = Math.random() < 0.5 ? 1 : -1;
        // console.log("", this._rotationDirection);
    }

    adjustRotationSpeedByDifficultyConfig(minRotationSpeedMultiplier, maxRotationSpeedMultiplier) {
        // 
        const speedMultiplier = Math.random() * (maxRotationSpeedMultiplier - minRotationSpeedMultiplier) + minRotationSpeedMultiplier;

        // 
        //  this.minRotationSpeed  this.maxRotationSpeed 
        // 
        this.rotationSpeed = Math.random() * (this.maxRotationSpeed - this.minRotationSpeed) + this.minRotationSpeed;
        this.rotationSpeed *= speedMultiplier;
    }


}
