import { _decorator, Component, Node, Prefab, instantiate, Vec3, UITransform, AudioSource, AudioClip, Animation, Label, Button } from 'cc';
import { Balloon } from './Balloon';
import { DifficultyLevel, difficultyConfig } from './DifficultyConfig';
const { ccclass, property } = _decorator;

@ccclass('GameController')
export class GameController extends Component {
    @property(Prefab)
    balloonPrefab: Prefab = null; //  Prefab

    @property(Node)
    playArea: Node = null; // 
    @property(AudioSource)
    audioSource: AudioSource = null; // AudioSource

    @property(AudioClip)       // 
    correctSound: AudioClip = null;

    @property(AudioClip)      // 
    wrongSound: AudioClip = null;

    @property(AudioClip)
    appearSound: AudioClip = null;  //

    @property(AudioClip)      // 
    scoreSoundUp: AudioClip = null;

    @property(AudioClip)      // 
    scoreSoundDown: AudioClip = null;

    @property(AudioClip)      // 
    timeOverSound: AudioClip = null;

    @property(AudioClip)      // 
    timeOverDingSound: AudioClip = null;
    @property(AudioClip)      // 
    bilingDingSound: AudioClip = null;
    @property(Node)     //gameOverUI
    gameOverUI: Node = null;
    @property(Node) //
    targetScoreUI: Node = null;
    @property(Node)     //GameUI
    playingGameUI: Node = null;

    @property(Node)     //MenuUI
    menuUI: Node = null;
    @property(AudioClip)
    levelUpSound: AudioClip = null; // 
    @property(AudioClip)
    levelUpVocalSound: AudioClip = null; // 
    @property(AudioClip)
    levelDownSound: AudioClip = null; // 

    @property(Label)
    levelChangeLabel: Label = null; // Label
    @property(Label)
    nextLevelInfoLabel: Label = null; // Label
    @property(Label)
    ganmeOverTitleLabel: Label = null; //   
    @property(Label)
    nextLevelInfoNumLabel: Label = null; // Num Label
    @property(Label)
    targetScoreLabel: Label = null; // Num Label
    @property(Node)
    levelChangeUI: Node = null;//

    @property(Node)     //
    correctImage: Node = null;

    @property(Node)    //
    wrongImage: Node = null;

    @property(Button)    //
    startGameButton: Button = null;

    @property(Button)    //
    restartGameButton: Button = null;

    @property(Button)    //
    risenGameButton: Button = null;

    @property(Label)   //
    countDownLabel: Label | null = null;

    @property(Label)   //
    scoreLabel: Label | null = null;
    @property(Label)   //
    currentLevelLabel: Label | null = null;

    currentLevelString: string | null = null;
    originLevelString: string | null = null;
    @property(Label)   //
    gameOverScoreLabel: Label | null = null;
    @property(Label)
    gameModeLabel: Label = null; // /

    private gameMode: string = 'NUM'; // 
    private sortOrder: string = 'ASC'; // 


    // @property
    maxBalloons: number = 5; // 
    @property
    minScale: number = 1; // 

    @property
    maxScale: number = 10; // 
    // @property
    maxRadius: number = 0.1;// 
    @property
    totalTime: number = 30; // 

    @property
    defaultDifficultyKey: string = 'kindergarten'; // 

    private currentTime: number = 0; // 
    private sortedNumbers: number[] = []; // 
    // 
    private generatedScales: number[] = [];
    private nextIndex: number = 0; // 
    private levelChangeUIAnimation: Animation = null; //
    flexibilityScore: number = 0; // 
    originScore: number = 0; //
    numbersToSpawn: number[] = []; // 
    nextCorrectNumber = null; // 
    isGameOver = false; // 
    private currentDifficultyLevel: DifficultyLevel; // 
    start() {
        this.levelChangeUIAnimation = this.levelChangeUI.getComponent(Animation);
        this.menuUI.active = true;
        this.gameOverUI.active = false;
        this.playingGameUI.active = false;
        this.startGameButton.node.on(Node.EventType.TOUCH_START, this.startGame, this);
        this.restartGameButton.node.on(Node.EventType.TOUCH_START, this.restartGame, this);
        this.risenGameButton.node.on(Node.EventType.TOUCH_START, this.startGame, this);
    }
    setDifficulty(level: string) {
        this.currentDifficultyLevel = difficultyConfig[level];
        console.log(this.currentDifficultyLevel, JSON.stringify(this.currentDifficultyLevel));
        this.maxBalloons = this.currentDifficultyLevel.maxBalloons;
        console.log("level", JSON.stringify(this.currentDifficultyLevel))
    }
    restartGame() {
        const levelKeys = Object.keys(difficultyConfig);
        this.defaultDifficultyKey = levelKeys[0];
        console.log("restartGame defaultDifficultyKey=", this.defaultDifficultyKey)
        this.startGame()
    }

    startGame() {
        // 
        this.setDifficulty(this.defaultDifficultyKey);
        this.menuUI.active = false;
        this.playingGameUI.active = true
        this.gameOverUI.active = false; // UI
        this.showTargetScoreUI();

    }
    showTargetScoreUI() {
        this.targetScoreUI.active = true;
        this.targetScoreLabel.string = this.currentDifficultyLevel.requiredScore.toString()
        this.scheduleOnce(() => {
          this.beginGame();
        }, 3); // 
    }
    beginGame() {
        this.targetScoreUI.active = false;
        this.audioSource.play();
        this.currentTime = this.totalTime;
        // 
        this.currentTime = this.currentDifficultyLevel.gameTime;
        this.countDownLabel.string = this.currentTime.toString();
        this.selectRandomGameMode()
        this.flexibilityScore = 0
        this.scoreLabel.string = this.flexibilityScore.toString();
        this.currentLevelString = difficultyConfig[this.defaultDifficultyKey].name
        this.levelChangeLabel.string = this.currentLevelString;
        this.schedule(this.updateTimer, 1);
        // 
        // this.updateGameMode();
        this.spawnBalloons();
    }
    selectRandomGameMode() {
        // 
        // 
        const gameModeOptions = this.currentDifficultyLevel.gameModes.gameMode;
        const sortOrderOptions = this.currentDifficultyLevel.gameModes.sortOrder;
        this.gameMode = gameModeOptions[Math.floor(Math.random() * gameModeOptions.length)];
        this.sortOrder = sortOrderOptions[Math.floor(Math.random() * sortOrderOptions.length)];
        // 
        this.updateGameModeLabel();
    }
    //  moveToNextLevel 
    moveToNextLevel() {
        const levelKeys = Object.keys(difficultyConfig);
        const currentIndex = levelKeys.indexOf(this.defaultDifficultyKey);
        const nextIndex = currentIndex + 1;
        if (nextIndex < levelKeys.length) {

            // 
            this.defaultDifficultyKey = levelKeys[nextIndex];
            console.log("", this.defaultDifficultyKey)
            this.setDifficulty(this.defaultDifficultyKey);
            // 

            // 

            // GameOverUI
            this.audioSource.playOneShot(this.levelUpSound, 1); // 
            this.resetGameState();
            this.startGame();


        } else {
            // 
            console.log("");
            // 
            this.showGameCompletionUI();
        }
    }
    showGameCompletionUI() {
        // 
        // UI
        // this.gameCompletionUI.active = true; // UIgameCompletionUI

        // 
        // this.audioSource.playOneShot(this.gameCompletionSound, 1);

        // 
        // this.finalScoreLabel.string = `: ${this.flexibilityScore}`; // LabelfinalScoreLabel
    }

    resetGameState() {
        // 
        // 
        this.flexibilityScore = 0;
        this.scoreLabel.string = '0';
        // ...
    }
    updateGameModeLabel() {
        let modeDescription = ""; // 

        if (this.gameMode === 'NUM') {
            modeDescription += "";
        } else if (this.gameMode === 'SIZE') {
            modeDescription += "";
        }
        modeDescription += this.sortOrder === 'ASC' ? "" : "";
        this.gameModeLabel.string = modeDescription;

    }
    updateGameMode() {
        const modes = ["", "", "", ""];
        const selectedMode = modes[Math.floor(Math.random() * modes.length)];
        this.gameModeLabel.string = selectedMode;

        // gameModesortOrder
        let gameMode = 'NUM'; // 
        let sortOrder = 'ASC'; // 
        // gameModesortOrder
        if (selectedMode.includes("")) {
            gameMode = 'SIZE'; // 
        }
        if (selectedMode.includes("")) {
            sortOrder = 'DESC'; // 
        }

        // 
        this.gameMode = gameMode;
        this.sortOrder = sortOrder;
        // 
        console.log(`${selectedMode}`);
    }
    updateTimer() {
        this.currentTime -= 1;
        if (this.currentTime <= 10 && this.currentTime > 0) {//
            if (this.audioSource && this.timeOverSound) {
                this.audioSource.playOneShot(this.timeOverSound, 1); // 
            }
        }
        if (this.countDownLabel) {  //
            this.countDownLabel.string = this.currentTime.toString();
        }
        if (this.currentTime <= 0) {
            this.unschedule(this.updateTimer);
            if (this.audioSource && this.timeOverDingSound) {
                this.audioSource.playOneShot(this.timeOverDingSound, 1); // 
            }
            this.gameOver();
        }

    }
    gameOver() {
        console.log("game over");
        this.destroyAllBalloons(); // 
        this.isGameOver = true
        this.playingGameUI.active = false
        this.gameOverUI.active = true;
        this.gameOverScoreLabel.string = this.originScore.toString();
        // console.log("currentLevelString", this.currentLevelString);
        this.currentLevelLabel.string = this.currentLevelString;
        this.audioSource.stop();
        this.scheduleOnce(() => {
            this.updateGameOverUIScore();
        }, 0.5); // 
        if (this.audioSource && this.timeOverDingSound) {
            this.audioSource.playOneShot(this.timeOverDingSound, 1); // 
        }

        let scoreNeededForNextLevel = this.currentDifficultyLevel.requiredScore - this.flexibilityScore;
        if (scoreNeededForNextLevel > 0) {
            const soundClip = this.scoreSoundDown;
            if (soundClip) {
                this.audioSource.playOneShot(this.scoreSoundUp, 0.3); // 
            }
            this.restartGameButton.node.active = true; // 
            this.risenGameButton.node.active = true; // 
            this.nextLevelInfoLabel.node.active = true; // 
            this.nextLevelInfoNumLabel.node.active = true; // 

            // 
            this.ganmeOverTitleLabel.string = "";
            this.nextLevelInfoLabel.string = "";

            this.nextLevelInfoNumLabel.string = scoreNeededForNextLevel.toString();

        } else {
            this.restartGameButton.node.active = false; // 
            this.risenGameButton.node.active = false; // 
            this.nextLevelInfoLabel.node.active = false; // 
            this.nextLevelInfoNumLabel.node.active = false; // 

            // 
            this.scheduleOnce(() => {
                this.moveToNextLevel(); // 
            }, 3); // 3
            // Label
            this.ganmeOverTitleLabel.string = ""
            this.scheduleOnce(() => {
                // const animation = this.gameOverUI.getComponent(Animation);
                // if (animation) {
                //     animation.play('GameOverUILeave');
                // }
                this.audioSource.playOneShot(this.levelUpVocalSound, 1); // 

            }, 4); //  
            // 

        }

    }
    restartSpawnBalloons() {
        // 
        this.destroyAllBalloons(); // 
        this.nextIndex = 0;
        this.selectRandomGameMode()
        this.spawnBalloons();
        // 
    }
    generateUniqueScale() { // minScalemaxScale
        let uniqueScale;
        const scaleRange = this.maxScale - this.minScale;
        const minAllowedDifference = 0.1; // 
        do {
            uniqueScale = Math.random() * scaleRange + this.minScale;
            // 
        } while (this.generatedScales.some(scale => Math.abs(scale - uniqueScale) < minAllowedDifference));

        return uniqueScale;
    }
    getRandomPosition(): Vec3 {
        const playAreaTransform = this.playArea.getComponent(UITransform);
        if (!playAreaTransform) {
            console.error("playArea lacks a UITransformComponent!");
            return new Vec3(0, 0, 0);
        }
        const playAreaWidth = playAreaTransform.width;
        const playAreaHeight = playAreaTransform.height;

        // PlayAreamaxRadius
        // maxRadiusPlayArea
        const x = (Math.random() * (playAreaWidth - this.maxRadius * 2)) + this.maxRadius - playAreaWidth / 2;
        const y = (Math.random() * (playAreaHeight - this.maxRadius * 2)) + this.maxRadius - playAreaHeight / 2;

        return new Vec3(x, y, 0);
    }

    generateSortedRandomNumbers(count: number): number[] {
        // 
        let numbers = new Set<number>();
        const { numberRange } = this.currentDifficultyLevel;
        const [rangeStart, rangeEnd] = numberRange;

        while (numbers.size < count) {
            let num = Math.floor(Math.random() * (rangeEnd - rangeStart + 1)) + rangeStart;
            numbers.add(num);
        }

        return Array.from(numbers).sort((a, b) => a - b);
    }
    spawnBalloons() {// 

        this.destroyAllBalloons(); // 
        this.sortedNumbers = this.generateSortedRandomNumbers(this.maxBalloons); // 54

        this.generatedScales = []; // 
        const { rotationSpeedMultiplierRange } = this.currentDifficultyLevel;
        const [minRotationSpeedMultiplier, maxRotationSpeedMultiplier] = rotationSpeedMultiplierRange;
        const spawnTime = Date.now(); // 
        this.sortedNumbers.forEach(number => {
            const balloon = instantiate(this.balloonPrefab);
            this.playArea.addChild(balloon);
            const uniqueScale = this.generateUniqueScale();

            this.generatedScales.push(uniqueScale);
            // console.log(`${number}`, uniqueScale)
            balloon.setPosition(this.getRandomPosition());
            // 
            const balloonScript = balloon.getComponent(Balloon);
            if (balloonScript) {
                balloonScript.setUniqueScale(uniqueScale); // // 
                balloonScript.setNumber(number, spawnTime);// 
                // 
                balloonScript.adjustRotationSpeedByDifficultyConfig(minRotationSpeedMultiplier, maxRotationSpeedMultiplier);
            }

            balloon.on(Node.EventType.TOUCH_START, () => this.checkBalloon(number, uniqueScale, balloonScript.spawnTime), this);
        });
        if (this.audioSource && this.appearSound) {
            this.audioSource.playOneShot(this.appearSound, 1); // 
        }
        this.generatedScales = Array.from(this.generatedScales).sort((a, b) => a - b) // 

        console.log("", this.sortedNumbers)
        console.log("", this.generatedScales)
    }
    checkBalloon(clickedNumber: number, clickedScale: number, spawnTime) {
        // console.log("", this.flexibilityScore)
        const clickTime = Date.now();
        const elapsedTime = (clickTime - spawnTime) / 1000; // 
        // console.log('', elapsedTime)
        let isCorrect = false;


        if (this.gameMode === 'NUM') {
            // 
            if (this.sortOrder === 'ASC') {
                isCorrect = this.sortedNumbers[this.nextIndex] === clickedNumber;
            } else { // 'descending'
                isCorrect = this.sortedNumbers[this.sortedNumbers.length - 1 - this.nextIndex] === clickedNumber;
            }
        } else if (this.gameMode === 'SIZE') {
            // 
            // clickedScaleexpectedScale
            let expectedScale = this.sortOrder === 'ASC' ? this.generatedScales[this.nextIndex] : this.generatedScales[this.generatedScales.length - 1 - this.nextIndex];
            console.log('', expectedScale, clickedScale);
            isCorrect = clickedScale === expectedScale;
        }

        if (isCorrect) {
            console.log("!", clickedNumber);
            this.updateScore(true, elapsedTime);
            this.nextIndex++; // 
            if (this.nextIndex >= this.sortedNumbers.length) {
                console.log("");
                if (this.audioSource && this.correctSound) {
                    this.audioSource.playOneShot(this.correctSound, 1); // 
                    this.showCheckImage(true); // 
                    // 
                    this.scheduleOnce(() => {
                        this.restartSpawnBalloons();
                    }, 0.3); // 
                }

            }
        } else {
            console.log("", clickedNumber);
            this.updateScore(false, elapsedTime);
            // ...
            if (this.audioSource && this.wrongSound) {
                this.audioSource.playOneShot(this.wrongSound, 1); // 
            }
            this.showCheckImage(false); // 
            this.scheduleOnce(() => {
                this.restartSpawnBalloons();
            }, 0.3); // 
        }
    }

    updateGameOverUIScore() {
        //  
        let gameOverScoreNumber = 0;
        let incrementStep = 1;
        if (this.flexibilityScore >= 30 && this.flexibilityScore < 100) {
            incrementStep = 3;
        } else if (this.flexibilityScore >= 100 && this.flexibilityScore < 300) {
            incrementStep = 5;
        } else if (this.flexibilityScore >= 300 && this.flexibilityScore < 600) {
            incrementStep = 7;
        } else if (this.flexibilityScore >= 600) {
            incrementStep = 11;
        }
        const totalSteps = Math.ceil(this.flexibilityScore / incrementStep);
        let currentStep = 0;
        const animation = this.gameOverScoreLabel.getComponent(Animation);
        // 
        if (animation) {
            animation.play('ScoreNumberPop');
        }

        const intervalDuration = 2000 / totalSteps; // 
        const timer = setInterval(() => {
            if (currentStep < totalSteps) {
                currentStep++;
                gameOverScoreNumber += incrementStep;
                if (this.scoreSoundUp && this.scoreSoundUp) {
                    this.audioSource.playOneShot(this.scoreSoundUp, 0.3);
                }
                this.gameOverScoreLabel.string = gameOverScoreNumber.toString();
            } else {
                clearInterval(timer);

                if (this.flexibilityScore !== gameOverScoreNumber) {
                    gameOverScoreNumber = this.flexibilityScore;
                    this.gameOverScoreLabel.string = gameOverScoreNumber.toString();
                }
            }
        }, intervalDuration); // 

    }

    // checkBalloon
    updateScore(isCorrect: boolean, elapsedTime: number = 0) {
        // 
        const oldLevelName = this.currentDifficultyLevel.name;
        // 
        let scoreIncrement = this.currentDifficultyLevel.scoreIncrement;
        // console.log("=" + scoreIncrement)
        let timeAdjustedScoreIncrement = this.calculateTimeAdjustedScoreIncrement(elapsedTime, scoreIncrement);
        // console.log("=" + timeAdjustedScoreIncrement)

        // 
        let scoreChange = isCorrect ? timeAdjustedScoreIncrement : -timeAdjustedScoreIncrement * 2;
        // 
        if (!isCorrect && this.flexibilityScore + scoreChange < 0) {
            scoreChange = -this.flexibilityScore; // 0

        }

        // 
        const totalSteps = Math.abs(scoreChange); // 
        let currentStep = 0;
        // 
        const animation = this.scoreLabel.getComponent(Animation);
        if (animation) {
            animation.play('ScoreNumberPop'); // 
        }

        const intervalDuration = 50; // 
        const timer = setInterval(() => {
            if (currentStep < totalSteps) {
                this.flexibilityScore += (isCorrect ? 1 : -1); // 1
                this.scoreLabel.string = this.flexibilityScore.toString();
                currentStep++;
            } else {
                clearInterval(timer); // 
            }
        }, intervalDuration);

        // 
        if (this.audioSource) {
            const soundClip = isCorrect ? this.scoreSoundUp : this.scoreSoundDown;
            if (soundClip) {
                this.audioSource.playOneShot(soundClip, 0.3); // 
            }
        }

        // // 
        // this.updateDifficultyBasedOnScore(this.flexibilityScore, scoreChange);
        // // 
        // const newLevelName = this.currentDifficultyLevel.name;
        // if (oldLevelName !== newLevelName) {
        //     // 
        //     // console.log('', oldLevelName, newLevelName)
        //     this.handleLevelChangeFeedback(oldLevelName, newLevelName);
        // }
    }
    // 
    calculateTimeAdjustedScoreIncrement(elapsedTime: number, scoreIncrement: number): number {
        const timeThresholds = [3, 4, 5, 6]; // 
        const scorePenalties = [0, 1, 2, 3]; // 

        let timeAdjustedScoreIncrement = scoreIncrement; // 

        // 
        for (let i = 0; i < timeThresholds.length; i++) {
            if (elapsedTime > timeThresholds[i]) {
                // 1
                timeAdjustedScoreIncrement = Math.max(scoreIncrement - scorePenalties[i], 1);
            } else {
                break; // 
            }
        }
        // 6
        if (elapsedTime > 6) {
            const extraTime = elapsedTime - 6;
            // 211
            const extraPenalty = Math.min(Math.floor(extraTime / 2), scoreIncrement - 1);
            timeAdjustedScoreIncrement = Math.max(scoreIncrement - extraPenalty, 1);
        }

        return timeAdjustedScoreIncrement;
    }


    handleLevelChangeFeedback(oldLevelName: string, newLevelName: string) {

        // 
        const oldIndex = Object.keys(difficultyConfig).findIndex(key => difficultyConfig[key].name === oldLevelName);
        const newIndex = Object.keys(difficultyConfig).findIndex(key => difficultyConfig[key].name === newLevelName);

        // ...
        if (newIndex > oldIndex) {
            // 
            this.audioSource.playOneShot(this.levelUpSound, 1);
            this.scheduleOnce(() => {
                this.audioSource.playOneShot(this.levelUpVocalSound, 1);
            }, 0.5); // 

        } else if (newIndex < oldIndex) {
            // 
            this.audioSource.playOneShot(this.levelDownSound, 1);
        }

        this.levelChangeUIAnimation.play('levelChange'); // 
        // 
        this.levelChangeLabel.string = `${newLevelName}`;


        this.currentLevelString = newLevelName


    }

    showCheckImage(isCorrect: boolean) {
        // 
        if (isCorrect) {
            this.correctImage.active = true;
            this.scheduleOnce(() => {
                this.correctImage.active = false;
            }, 0.3); // 1
        } else {
            this.wrongImage.active = true;
            this.scheduleOnce(() => {
                this.wrongImage.active = false;
            }, 0.3); // 1
        }
    }
    destroyAllBalloons() {
        //  playArea  
        this.playArea.children.slice().forEach(balloon => {
            //  Balloon 
            if (balloon.getComponent(Balloon)) {
                // 
                balloon.destroy();
            }
        });
    }
}
